"use client";

import extractErrorMessages from "@/src/app/utils/error.utils";
import { FormFieldService } from "@/src/services/formField.service";
import { UserFormService } from "@/src/services/userForm.service";
import CustomFormService from "@/src/services/customForm.service";
import { CustomField, UserForm } from "@/src/types/userForm/userForm.types";

import axios from "axios";
import React, {
  JSX,
  useState,
  useEffect,
  ChangeEvent,
  MouseEvent,
  useMemo,
} from "react";

import { toast } from "react-toastify";
import Skeleton from "react-loading-skeleton";
import { motion } from "framer-motion";
import Image from "next/image";
import { useRouter } from "next/navigation";

import Input from "@/src/components/ui/Input";
import Select from "@/src/components/ui/Select";
import ThemeToggle from "@/src/components/common/ThemeToggle";

import {
  MdOutlineFormatIndentIncrease,
  MdOutlineCloudUpload,
} from "react-icons/md";
import { GrDocumentUpdate } from "react-icons/gr";
import { FaArrowLeftLong, FaArrowRightLong, FaPlus } from "react-icons/fa6";
import { IoChevronBack } from "react-icons/io5";

import { FORM_FIELD_MODULE } from "@/src/constants/enum";

const page = (): JSX.Element => {
  const router = useRouter();

  /* -------------------- STATE -------------------- */
  const [err, setErr] = useState<string | string[]>("");
  const [loading, setLoading] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [fieldLoading, setFieldLoading] = useState(false);
  const [customFieldLoading, setCustomFieldLoading] = useState(false);

  const [userFormData, setUserFormData] = useState<UserForm | null>(null);

  const [globalFields, setGlobalFields] = useState<CustomField[]>([]);
  const [customFields, setCustomFields] = useState<CustomField[]>([]);

  const [selectedFieldsIds, setSelectedFieldsIds] = useState<string[]>([]);
  const [requiredFields, setRequiredFields] = useState<string[]>([]);

  /* -------------------- MERGED FIELDS -------------------- */
  const allFields = useMemo<CustomField[]>(() => {
    return [...globalFields, ...customFields];
  }, [globalFields, customFields]);

  /* -------------------- API CALLS -------------------- */
  const getUserForm = async () => {
    setIsLoading(true);
    try {
      const res = await UserFormService.getAllLead();
      if (res.status === 200 && res.data.data) {
        const selected = res.data.data.selectedFormFields ?? [];
        setUserFormData(res.data.data);
        setSelectedFieldsIds(selected.map((f: CustomField) => f.id));
        setRequiredFields(
          selected.filter((f: CustomField) => f.required).map(f => f.id)
        );
      }
    } catch (error) {
      handleError(error);
    } finally {
      setIsLoading(false);
    }
  };

  const getGlobalFields = async () => {
    setFieldLoading(true);
    try {
      const res = await FormFieldService.getLeadsAll();
      if (res.status === 200) setGlobalFields(res.data.data);
    } catch (error) {
      handleError(error);
    } finally {
      setFieldLoading(false);
    }
  };

  const getCustomFields = async () => {
    setCustomFieldLoading(true);
    try {
      const res = await CustomFormService.getAllByCreator();
      if (res.status === 200) setCustomFields(res.data.data);
    } catch (error) {
      handleError(error);
    } finally {
      setCustomFieldLoading(false);
    }
  };

  useEffect(() => {
    getUserForm();
    getGlobalFields();
    getCustomFields();
  }, []);

  /* -------------------- HANDLERS -------------------- */
  const handleError = (error: unknown) => {
    if (axios.isAxiosError(error)) {
      const messages = extractErrorMessages(error);
      setErr(messages);
      toast.error(`${messages}`);
    } else {
      setErr("Something went wrong");
      toast.error("Something went wrong");
    }
  };

  const handleChecked = (e: ChangeEvent<HTMLInputElement>) => {
    const { value, checked } = e.target;

    if (checked) {
      setSelectedFieldsIds(prev =>
        prev.includes(value) ? prev : [...prev, value]
      );
      return;
    }

    if (requiredFields.includes(value)) {
      toast.info("This field is required");
      return;
    }

    setSelectedFieldsIds(prev => prev.filter(id => id !== value));
  };

  const handleSubmit = async (e: MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    setLoading(true);

    try {
      if (!userFormData) {
        const res = await UserFormService.create({
          selectedFormFields: selectedFieldsIds,
          module: FORM_FIELD_MODULE.LEAD,
        });

        if (res.status === 201) {
          toast.success("Form created successfully");
          return;
        }
      }

      const res = await UserFormService.update(userFormData!.id, {
        selectedFormFields: selectedFieldsIds,
        module: FORM_FIELD_MODULE.LEAD,
      });

      if (res.status === 200) {
        toast.success("Form updated successfully");
      }
    } catch (error) {
      handleError(error);
    } finally {
      setLoading(false);
    }
  };

  /* -------------------- RENDER -------------------- */
  return (
    <div className="fixed min-h-screen w-full bg-stone-100 dark:bg-gray-800 overflow-y-scroll">

      {/* SIDEBAR */}
      <div className="fixed left-0 top-0 w-88 h-screen border-r bg-slate-50 dark:bg-gray-800/50 px-6 pt-12 pb-12 overflow-y-scroll">
        <div className="flex items-center gap-3 border-b pb-4 mb-4">
          <MdOutlineFormatIndentIncrease className="text-blue-500" />
          <h2 className="font-semibold text-lg">All Form Fields</h2>
        </div>

        <ul className="flex flex-col gap-3">
          {!fieldLoading && !customFieldLoading ? (
            allFields.map(field => (
              <li key={field.id} className="flex items-center gap-3">
                <input
                  type="checkbox"
                  value={field.id}
                  checked={selectedFieldsIds.includes(field.id)}
                  onChange={handleChecked}
                  className="peer"
                />
                <label className="peer-checked:text-blue-600 cursor-pointer">
                  {field.name}
                  {field.required && <span className="text-red-500">*</span>}
                </label>
              </li>
            ))
          ) : (
            Array.from({ length: 12 }).map((_, i) => (
              <Skeleton key={i} height={16} width={120} />
            ))
          )}
        </ul>

        <button className="mt-6 flex items-center gap-2 border px-4 py-2 rounded-md hover:bg-blue-600 hover:text-white">
          <FaPlus /> Add Custom Field
        </button>
      </div>

      {/* HEADER */}
      <div className="fixed left-96 top-4 right-6 flex items-center justify-between z-50">
        <div className="flex items-center gap-2">
          <GrDocumentUpdate className="text-blue-500" />
          <h2 className="text-2xl font-semibold">Update Lead Form</h2>
        </div>
        <div className="flex items-center gap-3">
          <button onClick={() => router.back()}><FaArrowLeftLong /></button>
          <button onClick={() => router.forward()}><FaArrowRightLong /></button>
          <ThemeToggle />
        </div>
      </div>

      {/* FORM PREVIEW */}
      <div className="ml-92 mt-20 p-8">
        <div className="bg-white dark:bg-gray-700 p-6 rounded-lg grid grid-cols-2 gap-5">
          {selectedFieldsIds.length === 0 ? (
            <div className="col-span-2 text-center">
              <Image src="/images/Cry.gif" alt="No fields" width={200} height={200} />
              <p>No fields selected</p>
            </div>
          ) : (
            allFields.map(field => {
              if (!selectedFieldsIds.includes(field.id)) return null;

              if (["text", "email", "number"].includes(field.type)) {
                return (
                  <motion.div key={field.id}>
                    <Input
                      name={field.key}
                      label={field.name}
                      type={field.type}
                      required={field.required}
                    />
                  </motion.div>
                );
              }

              if (field.type === "select") {
                return (
                  <motion.div key={field.id}>
                    <Select
                      name={field.key}
                      label={field.name}
                      options={field.options ?? []}
                      required={field.required}
                    />
                  </motion.div>
                );
              }

              return null;
            })
          )}

          {/* ACTIONS */}
          <div className="col-span-2 flex justify-end gap-4">
            <button
              onClick={handleSubmit}
              disabled={loading || selectedFieldsIds.length === 0}
              className="bg-blue-600 text-white px-4 py-2 rounded-md"
            >
              <MdOutlineCloudUpload />
              {loading ? "Saving..." : "Update Fields"}
            </button>
            <button onClick={() => router.back()} className="px-4 py-2">
              <IoChevronBack /> Back
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default page;
